import React from "react";
import { Link } from "react-router-dom";
import "./App.css";

class Profile extends React.Component {
  render() {
    return (
      <div className="profile-page fill-screen">
        <Link to="/">
          <button className="cancel" onClick={this.onClickCancel}>
            <img src="/cancel.svg" alt="Cancel" />
          </button>
        </Link>

        <div style={{ textAlign: "center" }}>
          <img
            src="/icon2-512.png"
            style={{ width: "30vw", height: "30vw" }}
          />
          <h3 style={{ fontSize: "14px" }}>{this.props.user.name}</h3>
          <p style={{ fontSize: "12px" }}>{this.props.user.email}</p>
        </div>

        <h3>Other links</h3>

        <RowButton label="HelloHunt" href="https://www.hellosign.com/" />
        <RowButton label="Moar Faxes" href="https://www.hellosign.com/" />
        <RowButton
          label="HelloFax"
          href="https://www.hellosign.com/products/hellofax"
        />
        <RowButton label="HelloSign" href="https://www.hellosign.com/" />
        <RowButton
          label="HelloWorks"
          href="https://www.hellosign.com/products/helloworks"
        />
      </div>
    );
  }
}

class RowButton extends React.Component {
  render() {
    return (
      <a href={this.props.href} target="_blank" className="row-button">
        <div className="label">{this.props.label}</div>
      </a>
    );
  }
}

export default Profile;
