import React from "react";
import * as THREE from "three";
import faceFilter from "facefilter";
import JeelizResizer from "./JeelizResizer";
import JeelizThreejsHelper from "./JeelizThreejsHelper";
import { Redirect } from "react-router";
import {
  DotScreenEffect,
  EffectComposer,
  HueSaturationEffect,
  EffectPass,
  RenderPass,
  BrightnessContrastEffect,
  VignetteEffect,
  PixelationEffect,
  GlitchEffect,
  GlitchMode
} from "postprocessing";
import Slider from "react-slick";
import "./App.css";
import { Vector2 } from "three";

class Camera extends React.Component {
  constructor(props) {
    super(props);

    this.detect_callback = this.detect_callback.bind(this);
    this.onInitialResizeReady = this.onInitialResizeReady.bind(this);
    this.onFaceFilterInitReady = this.onFaceFilterInitReady.bind(this);
    this.initThreeScene = this.initThreeScene.bind(this);
    this.onTrack = this.onTrack.bind(this);
    this.onShutter = this.onShutter.bind(this);
    this.onCarouselChange = this.onCarouselChange.bind(this);

    this.state = {
      redirect: null,
      currentPhoto: null
    };

    this.sliderSettings = {
      dots: false,
      infinite: true,
      speed: 500,
      slidesToShow: 3,
      slidesToScroll: 1,
      centerMode: true,
      afterChange: this.onCarouselChange
    };

    this.effects = [
      {
        name: "Default",
        icon: ""
      },
      {
        name: "1",
        bg: "rgba(255, 255, 255, 0.5)",
        icon: "/img/lightbulb.svg"
      },
      {
        name: "2",
        bg: "rgba(0, 0, 0, 0.5)",
        icon: "/img/halftone.svg"
      },
      {
        name: "3",
        bg: "rgba(255, 255, 255, 0.5)",
        icon: "/img/boxy.svg"
      },
      {
        name: "4",
        bg: "rgba(0, 0, 0, 0.5)",
        icon: "/img/vignette.svg"
      },
      {
        name: "5",
        bg: "rgba(255, 255, 255, 0.5)",
        icon: "/img/pixel.png"
      },
      {
        name: "6",
        bg: "rgba(255, 255, 255, 0.5)",
        icon: "/img/glitch.png"
      },
    //   {
    //     name: "7",
    //     bg: "rgba(255, 255, 255, 0.5)",
    //     icon: "/stories.svg"
    //   }
    ];
  }

  detect_callback(faceIndex, isDetected) {
    if (isDetected) {
      console.log("INFO in detect_callback() : DETECTED");
    } else {
      console.log("INFO in detect_callback() : LOST");
    }
  }

  initThreeScene(spec) {
    const threeStuffs = JeelizThreejsHelper.init(spec, this.detect_callback);
    const { scene, renderer, faceObject } = threeStuffs;
    this.faceObject = faceObject;
    this.ctx = spec.GL;

    // CREATE A CUBE
    const cubeGeometry = new THREE.BoxGeometry(1, 1, 1);

    var loader = new THREE.TextureLoader();
    loader.setPath("/");
    var texture = loader.load("./icon512.png");
    texture.wrapS = THREE.RepeatWrapping;
    texture.wrapT = THREE.RepeatWrapping;
    const material = new THREE.MeshBasicMaterial({
      map: texture
    });
    this.threeCube = new THREE.Mesh(cubeGeometry, material);
    this.threeCube.frustumCulled = false;
    //threeStuffs.faceObject.add(threeCube);

    //CREATE THE CAMERA
    var aspecRatio = spec.canvasElement.width / spec.canvasElement.height;
    this.camera = new THREE.PerspectiveCamera(20, aspecRatio, 0.1, 100);

    // Prepare postprocessing
    this.composer = new EffectComposer(renderer);
    this.composer.addPass(new RenderPass(scene, this.camera));

    // Create effects
    const saturationEffect = new HueSaturationEffect({ saturation: -1 });
    const brightnessContrastEffect = new BrightnessContrastEffect({
      contrast: 0.5
    });
    const dotScreenEffect = new DotScreenEffect({ scale: 2 });

    // Add the effects
    this.effectPass = null;
    this.onCarouselChange(0);
    // this.effectPass = new EffectPass(
    //   this.camera,
    //   saturationEffect,
    //   brightnessContrastEffect,
    //   dotScreenEffect
    // );
    // this.effectPass.renderToScreen = true;
    // this.composer.addPass(this.effectPass);
  }

  onInitialResizeReady(isError, bestVideoSettings) {
    //init_faceFilter(bestVideoSettings);
    console.log("best", bestVideoSettings);
    console.log("About to init");
    console.log(faceFilter);
    faceFilter.init({
      //you can also provide the canvas directly
      //using the canvas property instead of canvasId:
      canvasId: "jeeFaceFilterCanvas",
      NNCpath: "NNC.json", //path to JSON neural network model (NNC.json by default)
      callbackReady: this.onFaceFilterInitReady, //end callbackReady()

      //called at each render iteration (drawing loop)
      callbackTrack: this.onTrack
    }); //end init call
  }

  onFaceFilterInitReady(errCode, spec) {
    if (errCode) {
      console.log("AN ERROR HAPPENS. ERROR CODE =", errCode);
      return;
    }

    console.log("INFO: JEEFACEFILTERAPI IS READY");
    this.initThreeScene(spec);
  }

  onTrack(detectState) {
    JeelizThreejsHelper.render(detectState, this.camera, this.composer);
  }

  componentDidMount() {
    console.log(JeelizResizer);
    console.log("about to resize");
    JeelizResizer.size_canvas({
      canvasId: "jeeFaceFilterCanvas",
      callback: this.onInitialResizeReady
    });
  }

  onShutter() {
    // Store current canvas image somewhere, change the view to edit mode
    const canvas = document.getElementById("jeeFaceFilterCanvas");
    localStorage.setItem("photo", canvas.toDataURL());
    console.log("Setting photo to local");
    //this.setState({ redirect: "/edit" });
    window.location.hash = "/edit";
  }

  onClickFlip() {
    //faceFilter.update_video
    alert("Flip");

    //const videoElement = null;
    //faceFilter.update_videoElement(videoElement, () => {})
  }

  openProfile() {
    window.location.hash = "/profile";
  }

  onCarouselChange(index) {
    console.log("Requested effect " + index);

    // Remove 3D
    this.faceObject.remove(this.threeCube);

    // Create effects array depending on selected filter
    let effects;
    switch (index) {
      case 0:
        // Default effect
        effects = [
          new HueSaturationEffect({ saturation: -1 }),
          new BrightnessContrastEffect({ contrast: 0.5 }),
          new DotScreenEffect({ scale: 2 })
        ];
        break;
      case 1:
        // Light
        effects = [
          new HueSaturationEffect({ saturation: -1 }),
          new BrightnessContrastEffect({ contrast: 0.0, brightness: 0.75 }),
          new DotScreenEffect({ scale: 1 })
        ];
        break;
      case 2:
        effects = [
          new HueSaturationEffect({ saturation: -1 }),
          new BrightnessContrastEffect({ contrast: 0.7, brightness: 0.25 }),
          new DotScreenEffect({ scale: 0.75, dotAngle: 55 })
        ];
        break;
      case 3:
        effects = [
          new HueSaturationEffect({ saturation: -1 }),
          new BrightnessContrastEffect({ contrast: 0.5 }),
          new DotScreenEffect({ scale: 2 })
        ];
        this.faceObject.add(this.threeCube);
        break;
      case 4:
        // Vignette
        effects = [
          new HueSaturationEffect({ saturation: -1 }),
          new BrightnessContrastEffect({ contrast: 0.6, brightness: 0.4 }),
          new VignetteEffect(),
          new DotScreenEffect({ scale: 1 })
        ];
        break;
      case 5:
        // Low Q
        effects = [
          new HueSaturationEffect({ saturation: -1 }),
          new BrightnessContrastEffect({ contrast: 1 }),
          new PixelationEffect(7)
        ];
        break;
      case 6:
        // Glitch
        effects = [
          new HueSaturationEffect({ saturation: -1 }),
          new BrightnessContrastEffect({
            contrast: 0.6,
            brightness: 0.4
          }),
          new GlitchEffect({
            //duration: new Vector2(20000, 40000),
            //delay: new Vector2(20000, 40000),
            glitchMode: GlitchMode.SPORADIC,
          }),
          new DotScreenEffect({ scale: 1 })
        ];
        break;
      default:
        alert("Filter not defined!");
        break;
    }

    // Build new EffectPass
    const pass = new EffectPass(this.camera, ...effects);
    pass.renderToScreen = true;

    // Replace old pass with new one
    if (this.effectPass !== null) {
      this.composer.removePass(this.effectPass);
    }
    this.effectPass = pass;
    this.composer.addPass(this.effectPass);
  }

  render() {
    const redirect = this.state.redirect;
    if (redirect) {
      return <Redirect to={redirect} />;
    }

    return (
      <div className="camera-view fill-screen">
        <canvas width="360" height="640" id="jeeFaceFilterCanvas" />
        <button
          className="avatar"
          style={{
            backgroundImage: `url(${this.props.userImageUrl})`,
            backgroundSize: "cover"
          }}
          onClick={this.openProfile}
        />
        <button className="flip" onClick={this.onClickFlip}>
          <img src="/flip.svg" alt="Flip" />
        </button>
        <button className="shutter" onClick={this.onShutter} />

        <Slider {...this.sliderSettings} className="effect-slider">
          {this.effects.map((effect, index) => (
            <EffectThumb key={index} icon={effect.icon} bg={effect.bg} />
          ))}
        </Slider>
        <button className="stories">
          <img src="/stories.svg" alt="Stories" />
        </button>
      </div>
    );
  }
}

class EffectThumb extends React.Component {
  render() {
    return (
      <div>
        <div
          className="effect-thumb"
          style={{ backgroundImage: `url(${this.props.icon})`, backgroundColor: this.props.bg }}
        >
          {this.props.name}
        </div>
      </div>
    );
  }
}

export default Camera;
