import React from "react";
import { HashRouter as Router, Route, Link } from "react-router-dom";
import Camera from "./Camera";
import Edit from "./Edit";
import Send from "./Send";
import Login from "./Login";
import Profile from "./Profile";
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";
import "./App.css";

class App extends React.Component {
  constructor(props) {
    super(props);

    // Prebind
    this.initSigninV2 = this.initSigninV2.bind(this);
    this.onSigninChanged = this.onSigninChanged.bind(this);
    this.onUserChanged = this.onUserChanged.bind(this);

    this.state = {
      authState: "uninitialized"
    };
  }

  componentDidMount() {
    console.log("gapi", window.gapi);

    // Determine if authenticated
    window.gapi.load("auth2", this.initSigninV2);
  }

  initSigninV2() {
    console.log("initted");
    const auth2 = window.gapi.auth2.init({
      client_id:
        "410718998162-6okhe5f8m26vn0fhdjeu9gp1edma34uh.apps.googleusercontent.com",
      scope: "profile"
    });

    // Listen for sign-in state changes.
    auth2.isSignedIn.listen(this.onSigninChanged);

    // Listen for changes to current user.
    auth2.currentUser.listen(this.onUserChanged);

    // Sign in the user if they are currently signed in.
    if (auth2.isSignedIn.get() === true) {
      auth2.signIn();
    }

    // Start with the current live values.
    //refreshValues();
  }

  /**
   * Listener method for sign-out live value.
   *
   * @param {boolean} val the updated signed out state.
   */
  onSigninChanged(val) {
    console.log("Signin state changed to ", val);
    //debugger;
  }

  /**
   * Listener method for when the user changes.
   *
   * @param {GoogleUser} user the updated user.
   */
  onUserChanged(user) {
    console.log("user changed", user);

    if (user.getId() === null) {
      this.setState({ authState: "unauthenticated" });
      return;
    }

    const profile = user.getBasicProfile();
    this.setState({
      authState: "authenticated",
      user: {
        name: profile.getName(),
        email: profile.getEmail(),
        imageUrl: profile.getImageUrl()
      },
      userName: profile.getName(),
      userImageUrl: profile.getImageUrl()
    });
    //debugger;
  }

  render() {
    const { authState } = this.state;

    if (authState === "uninitialized") {
      return "";
    }

    if (authState === "unauthenticated") {
      return <Login />;
    }

    if (authState === "authenticated") {
      return (
        <div className="fill-screen">
          <Camera userImageUrl={this.state.userImageUrl} />
          <Router>
            <Route exact path="/" />
            <Route path="/edit" component={Edit} />
            <Route path="/send" component={Send} />
            <Route
              path="/profile"
              render={props => <Profile {...props} user={this.state.user} />}
            />
          </Router>
        </div>
      );
    }

    console.error("Auth state is: ", authState);
    return <div>There was a problem :(</div>;
  }
}

export default App;
