import React from "react";
import { Redirect } from "react-router";
import "./App.css";

class Send extends React.Component {
  constructor(props) {
    super(props);
    let redirect = null;
    this.state = {
      redirect: redirect,
      sending: false,
      loadingText: "Sending..."
    };

    this.onClickCancel = this.onClickCancel.bind(this);
    this.onSelect = this.onSelect.bind(this);
    this.updateLoader = this.updateLoader.bind(this);
  }

  componentDidMount() {}

  onClickCancel() {
    this.setState({ redirect: "/edit" });
  }

  onSelect() {
    // TMP: Send fax now
    /* eslint-disable no-restricted-globals */
    if (!confirm("Send this fax?")) {
      return;
    }

    this.setState({ sending: true });

    // Play awful noise
    this.audio = new Audio("/noise.m4a");
    this.audio.onended = () => {
      this.setState({ sending: false, redirect: "/" });
    };
    this.audio.play();

    setTimeout(this.updateLoader, 1000);
    fetch("https://stepheneisenhauer.com/misc/sfx.php", {
      method: "post",
      body: JSON.stringify({
        image: localStorage.getItem("photo")
      })
    })
      .then((response) => {
        console.log("Send fax 1");
      })
      .then((data) => {
        console.log("Send fax 2");
        this.setState({ sending: false });
      });
  }

  updateLoader() {
    if (this.state.sending) {
      this.setState({
        loadingText: {
          "Sending...": "Sending",
          Sending: "Sending.",
          "Sending.": "Sending..",
          "Sending..": "Sending..."
        }[this.state.loadingText]
      });
      setTimeout(this.updateLoader, 1000);
    }
  }

  render() {
    const redirect = this.state.redirect;
    if (redirect) {
      return <Redirect to={redirect} />;
    }

    let sendingElement = null;
    if (this.state.sending) {
      sendingElement = (
        <div className="sendingModal">{this.state.loadingText}</div>
      );
    }

    const friends = [
      {
        name: "Milo",
        photo: "https://www.fillmurray.com/g/64/64",
        number: "12345"
      },
      {
        name: "Grover",
        photo: "https://www.fillmurray.com/g/72/72",
        number: "12345"
      },
      {
        name: "Genie",
        photo: "https://www.fillmurray.com/g/48/48",
        number: "12345"
      },
      {
        name: "Opi",
        photo: "https://www.fillmurray.com/g/56/56",
        number: "12345"
      },
      {
        name: "Holly",
        photo: "https://www.fillmurray.com/g/84/84",
        number: "12345"
      },
      {
        name: "Dougie",
        photo: "https://www.fillmurray.com/g/64/64",
        number: "12345"
      }
    ];

    const friendRows = friends.map((friend, idx) => (
      <DestinationRow name={friend.name} key={idx} photo={friend.photo} onClick={this.onSelect} />
    ));

    return (
      <div className="send-page fill-screen">
        {sendingElement}
        <button className="cancel" onClick={this.onClickCancel}>
          <img src="/cancel.svg" alt="Cancel" />
        </button>
        <h2>Friends</h2>
        <ul>{friendRows}</ul>
      </div>
    );
  }
}

class DestinationRow extends React.Component {
  render() {
    return (
      <li onClick={this.props.onClick}>
        <div className="icon" style={{backgroundImage: `url(${this.props.photo})`}} />
        <div className="label">{this.props.name}</div>
        <div className="chevron">›</div>
      </li>
    );
  }
}

export default Send;
